.player {
  margin:0 auto;
  margin-top: 28px;
  padding:24px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  gap: 28px;
  height: 100%;
  overflow: hidden;
}

.player__item-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  border-radius: 16px;
  aspect-ratio: 1/1;
  cursor: pointer;
  filter: brightness(80%);
}

.player__text {
  margin: 0;
  padding: 0;
  display: flex;
  flex-flow: column;
  justify-content: start;
}

.player__name {
  padding: 0;
  margin: 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.player__theme {
  padding: 0;
  margin: 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.player__player-buttons {
  margin: 0 auto;
  padding: 0;
  width:100%;
  max-width: 288px;
  display: flex;
  flex-flow:row;
  justify-content:space-between;
}

.player__player-button {
  cursor: pointer;
}
