@import url(./vendor/fonts.css);

:root {
  margin:0;
  padding:0;
  --local-border-radius: 10px;
}

body {
  margin: 0 auto;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
  background-color: var(--tg-theme-bg-color);
  font-family: 'SF';
  font-weight: normal;
  max-width: 390px;
  overflow-y: scroll;
  scrollbar-width: none;
  /* Firefox ? */
  -ms-overflow-style: none;
  /* IE, Edge */
}

body::-webkit-scrollbar {
  display: none;
  /* Chrome, Opera, Safari */
}

.text {
  font-family: 'SF';
  display: block;
  display: -webkit-box;
}

.text_color_main {
  color:var(--tg-theme-text-color);
}

.text_color_hint {
  color:var(--tg-theme-hint-color);
}

.text_color_white {
  color: white;
}

.text_color_button {
  color:var(--tg-theme-button-text-color);
}

.text_weight_regular {
  font-weight: 400;
}

.text_weight_medium {
  font-weight: 590;
}

.text_weight_bold {
  font-weight: 700;
}

.text_size_xs {
  font-size: 13px;
  line-height: 16px;
}

.text_size_s {
  font-size: 15px;
  line-height: 20px;
}

.text_size_sm {
  font-size: 16px;
  line-height: 21px;
}

.text_size_m {
  font-size: 17px;
  line-height: 22px;
}

.text_size_l {
  font-size: 20px;
  line-height: 25px;
}

.text_size_xl {
  font-size: 24px;
  line-height: 28px;
}

.text_overflow_one {
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  overflow: hidden;
}

.text_overflow_two {
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  overflow: hidden;
}

.text_overflow_three {
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  overflow: hidden;
}

.text_overflow {
  -webkit-line-clamp: 10;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  overflow: hidden;
}

.text_style_uppercase {
  text-transform: uppercase;
}