.loader__content {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  margin-top: 15px;
  border-radius: 50%;
  display: block;
}

.loader__content i::after {
  position: absolute;
  clip: rect(0, 2em, 1em, 0.5em);
  left: calc(50vw - 13px);
  width: 26px;
  height: 26px;
  content: '';
  animation: loader-circle 0.8s ease-in-out infinite;
  border-radius: 50%;
  box-shadow: inset 0 0 0 0.1em var(--tg-theme-button-text-color);
}

@keyframes loader-circle {
  0% {
    transform: rotate(-180deg);
  }

  100% {
    transform: rotate(180deg);
  }
}