.list {
  margin: 0 auto;
  padding: 0;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: start;
  /*   overflow:hidden; */
  overflow-y: scroll;
  scrollbar-width: none;
  /* Firefox ? */
  -ms-overflow-style: none;
  /* IE, Edge */
}

.list::-webkit-scrollbar {
  display: none;
  /* Chrome, Opera, Safari */
}

.list__title {
  margin: 0;
  padding: 16px 16px 12px 16px;
  z-index: 2;
  /*   color: white; */
}

.list__text-horizontal-card {
  margin: 0;
  z-index: 2;
  /*   color: white; */
}

.list__slider_type_horizontal {
  margin: 0;
  margin-bottom: 12px;
  padding: 0 0 0 12px;
  width: calc(100% - 12px);
  list-style-type: none;
  box-sizing: content-box;
  display: flex;
  flex-flow: row;
  gap: 12px;
  justify-content: flex-start;
  overflow-x: scroll;
}

/* Выключение скролла */
.list__slider_type_horizontal {
  -ms-overflow-style: none;
  /* Internet Explorer 10+ */
  scrollbar-width: none;
  /* Firefox */
}

.list__slider_type_horizontal::-webkit-scrollbar {
  display: none;
  /* Safari and Chrome */
}

.list__card_type_horizontal {
  margin: 0;
  padding: 16px;
  min-width: 252px;
  height: 312px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 16px;
  position: relative;
  display: flex;
  flex-flow: column;
  justify-content: flex-end;
  gap: 4px;
}

.list__card_type_horizontal::before {
  z-index: 0;
  content: '';
  display: block;
  top: 0;
  left: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  /* background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.0527407) 8.07%, rgba(0, 0, 0, 0.103831) 15.08%, rgba(0, 0, 0, 0.15411) 21.23%, rgba(0, 0, 0, 0.204418) 26.69%, rgba(0, 0, 0, 0.255596) 31.67%, rgba(0, 0, 0, 0.308484) 36.36%, rgba(0, 0, 0, 0.36392) 40.96%, rgba(0, 0, 0, 0.422746) 45.65%, rgba(0, 0, 0, 0.485802) 50.63%, rgba(0, 0, 0, 0.553928) 56.09%, rgba(0, 0, 0, 0.627963) 62.23%, rgba(0, 0, 0, 0.708747) 69.23%, rgba(0, 0, 0, 0.797122) 77.3%, rgba(0, 0, 0, 0.893926) 86.63%, #000000 97.4%); */
  background: linear-gradient(to top, black, 20%, transparent);
  border-radius: 16px;
  /*   opacity:60%; */
}

.list__flex_flow_row {
  display: flex;
  flex-flow: row;
  justify-content: flex-start;
  align-items: center;
  gap: 4px;
}

.list__slider_type_vertical {
  margin: 0;
  padding: 0 12px 0 12px;
  width: calc(100% - 24px);
  list-style-type: none;
  display: flex;
  flex-flow: column;
  gap: 8px;
  justify-content: flex-start;
}

.list__card_type_vertical {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 312px;
  border-radius: 16px;
  display: flex;
  flex-flow: column;
  justify-content: flex-start;
  gap: 0px;
  background-color: var(--tg-theme-secondary-bg-color);
}

.list__image-fullwidth {
  width: 100%;
  height: 190px;
  object-fit: cover;
  object-position: center;
  border-radius: 16px 16px 0 0;
}

.list__text-vertical-card {
  margin: 0;
  padding: 12px 16px 16px 16px;
  display: flex;
  flex-flow: column;
  justify-content: flex-start;
}

.list_text-vertical-card-title {
  margin: 0;
  margin-bottom: 4px;
}

.list_text-vertical-card-description {
  margin: 0;
  margin-bottom: 12px;
  max-height: 40px;
}