.course {
  margin: 0;
  padding: 0;
  padding-bottom: 16px;
  width: 100%;
  display: flex;
  flex-flow: column;
  justify-content: flex-start;
  box-sizing: border-box;
  gap: 20px;
  background-color: var(--tg-theme-bg-color);
}

.course__text {
  margin: 0 auto;
  padding: 0;
  padding: 0 16px 16px 16px;
  width: 100%;
  display: flex;
  flex-flow: column;
  box-sizing: border-box;
  justify-content: flex-start;
  gap: 0px;
}

.course__image-fullwidth {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 240px;
  object-fit: cover;
  object-position: center;
}

.course__title {
  margin: 0;
  margin-bottom: 4px;
  padding: 0;
  box-sizing: border-box;
}

.course__flex_flow_row {
  display: flex;
  flex-flow: row;
  justify-content: flex-start;
  align-items: center;
  gap: 4px;
}

.course__section-title {
  margin: 0;
  margin-top: 12px;
  padding: 16px 16px 8px 16px;
}

.course__section-description {
  margin: 0;
  width: 100%;
  padding: 12px 11.5px 12px 11.5px;
  box-sizing: border-box;
  border-radius: 16px;
  background-color: var(--tg-theme-secondary-bg-color);
}

.course__theme-list {
  margin: 0;
  padding: 0;
  width: 100%;
  list-style-type: none;
  display: flex;
  flex-flow: column;
  gap: 0px;
  justify-content: flex-start;
  border-radius: 16px;
  background-color: var(--tg-theme-secondary-bg-color);
}


.course__theme {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 72px;
  list-style-type: none;
  display: flex;
  flex-flow: row;
  gap: 0px;
  justify-content: space-between;
  align-items: center;
}

.course__image-layout {
  margin: 0;
  padding: 16px;
  display: flex;
  justify-content: center;
  align-content: center;
}

.course__theme-icon {
  width: 40px;
  height: 40px;
  object-fit: cover;
  object-position: center;
  border-radius: 8px;
  aspect-ratio: 1;
}

.course__theme-text {
  margin: 0;
  padding: 0;
  width: fit-content;
  display: flex;
  flex-flow: column;
  gap: 4px;
  justify-content: space-around;
  align-items: flex-start;
  flex-grow: 1;
}

.course__theme-title {
  margin: 0;
  padding: 0;
}

.course__divider {
  margin: 0;
  padding: 0;
  width: 80%;
  align-self: flex-end;
  border-bottom: 0.3px solid var(--tg-theme-hint-color);
  opacity: 50%;
}

.course__divider:last-of-type {
  border-bottom: none;
}